import React from "react";
import { CustomColumns, Schedule } from "../../../types";
import { useQuery } from "react-query3";
import dayjs from "dayjs";
import Table from "components/ui/Table";
import localizedFormat from "dayjs/plugin/localizedFormat";
import { usePageContext } from "components/pages/Page";
import { formatDate } from "components/utils/utils";
import { Space } from "antd";
import DeleteButton from "components/ui/DeleteButton";
import EditModal from "components/ui/EditModal";
import { Modal } from "components/ui/Modal";

dayjs.extend(localizedFormat);

const columns: CustomColumns<Schedule>[] = [
  {
    title: "Description",
    dataIndex: "description",
    key: "description",
  },
  {
    title: "Next Execution",
    key: "nextExecution",
    render: (text, record: Schedule) => formatDate(record?.nextExecution),
  },
  {
    title: "Environment",
    key: "environment",
    dataIndex: "environment",
    render: (text) => (text ? text : "Default"),
  },
  {
    key: "runAs",
    title: "Run As",
    render: (text, record) => record?.credential || "Default",
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    editable: false,
    render: (text, record) => {
      return (
        <Space>
          <Modal>
            <EditModal record={record} schemaName="schedule" allowedWithOneWayGitSync={false} />
          </Modal>
          <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
        </Space>
      );
    },
  },
];

export default function ScriptSchedules() {
  const { resource: script } = usePageContext();
  const { data: schedules, isLoading } = useQuery<Schedule[]>(
    `/schedule`,
    {
      select: (data) => {
        return data.filter(schedule => schedule?.script === script?.fullPath)
      },
      enabled: !!script,
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }
  );

  return (
    <Table
      columns={columns}
      data={schedules}
      loading={isLoading}
    />
  );
}
